.container {
  padding: 0 1rem;
}

a {
  all: unset !important;
  color: inherit;
}
.header {
  display: flex;
  align-items: center;
  background: #eee; }

ul,
li {
  all: unset; }

nav {
  flex: 2 1; }

.header__logo {
  flex: 1 1; }

.header__logo > img {
  width: 100%;
  max-width: 100px;
  height: 30%; }

.header__list {
  display: flex;
  justify-content: space-around;
  font-size: 1.3rem;
  color: #424242; }

.header__item {
  cursor: pointer;
  padding: 0.5rem 1rem; }
  .header__item:hover {
    background: #464641;
    color: #fff;
    border-radius: 5px; }

table {
  border-collapse: collapse;
  width: 100%; }

table,
th,
td {
  border: 1px solid #424242ad; }

thead {
  background: #05cf05;
  color: #eee; }
  thead th {
    height: 31px; }

tbody tr:hover {
  background: #eee; }

.country-header {
  display: flex;
  flex-direction: column;
  margin: 1rem 0.5rem; }
  .country-header .country-flag {
    width: 100%;
    height: 100%; }
  .country-header img {
    width: 50%;
    height: 100%;
    object-fit: contain; }

.country-details {
  display: flex;
  flex-direction: column;
  font-size: 1.5rem; }
  .country-details > li {
    text-align: left;
    margin: 0.6rem 0; }

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

